<template>
	<view class="flex flex-direction padding">
		<view class="cu-modal " :class="ModalName" @tap="hideModal">
			<view class="cu-dialog" @tap.stop="">
				<view class="cu-bar bg-white justify-end">
					<view class="content text-bold text-cut">{{ isEdit? '编辑产品型号':'添加产品型号' }}</view>
					<view class="action" @tap="hideModal">
						<text class="cuIcon-close text-red"></text>
					</view>
				</view>
				<uni-forms ref="form" :value="current" validate-trigger="bind" err-show-type="undertext">
					<uni-forms-item name="Name">
						<view class="cu-form-group solid-bottom">
							<text class="title text-bold">名称</text>
							<input placeholder="请输入名称" :value="current.Name"
								@input="changName($event);binddata('Name', $event.detail.value,'form')" />
						</view>
					</uni-forms-item>
					<uni-forms-item name="CategoryId">
						<view class="cu-form-group solid-bottom">
							<text class="title text-bold">产品分类</text>
							<uni-data-picker placeholder="请选择产品分类" style="width: 75%;" :preload="true"
								:localdata="category_list" popup-title="产品分类"  @nodeclick="changeCategoryId" :value="current.CategoryId" >
							</uni-data-picker>
						</view>
					</uni-forms-item>
					<uni-forms-item>
						<view class="cu-form-group solid-bottom" name="Sort">
							<text class="title text-bold">排序</text>
							<uni-number-box min="0" @change="changeSort" :value="current.Sort"></uni-number-box>
						</view>
					</uni-forms-item>
				</uni-forms>
				<view class="cu-bar bg-blue" @tap="save('form')">
					<view class="action  margin-0 flex-sub  solid-left">{{isEdit ? '编辑':'添加'}}</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	/**
	 * 添加/编辑产品型号
	 * @author 陆彦捷
	 * @date 2021.06.30 添加/编辑产品型号
	 * @date 2021.07.23 重新引用login
	 * @description  
	 */
	import organ from '@/common/storage/organstorage.js';
	import categories from '@/common/data/category.js';
	import changeobj from '@/common/function/changeobjectecordname.js'; // 格式化值
	
	var _self;
	export default {
		name:"model-add",
		props: {
			// 当前组织id
			MchId: {
				type: Number,
				default: 0
			},
			// 请求Token
			Token: {
				type: String,
				default: ''
			},
			// 产品型号Id
			ModelId :{
				type: Number,
				default: 0,
			},
			// 分类id（用于加载所属分类）
			CategoryId :{
				type: Number,
				default: 0,
			},
			// 是否编辑状态
			isEdit :{
				type:Boolean,
				default:false
			},
		},
		data() {
			return {
				// 与样式有关参数
				ModalName: 'show', // 当前组织
				// 当前值
				current: {
					ModelId:  0,		// 型号
					MchId: this.MchId,	// 当前组织id
					CategoryId: 0,		// 设备分类
					Name: "",			// 名称
					Sort: 1,			// 排序
				},
				category_list: [], // 上级分类
				// 验证
				rules: {
					Name: {
						rules: [{
							required: true,
							errorMessage: '请输入名称'
						}]
					},
				},
			};
		},
		onReady: function() {
			// 初始化表单验证
			this.$refs.form.setRules(this.rules);
		},
		created: function() {
			_self = this;
			this.init();		// 初始化
			this.categories_request(); // 获取产品分类请求
			if(this.isEdit){
				this.model_request();		// 获取当前产品型号
			}
		},
		methods: {
			// ================================================ 初始化 ===========================================
			init : function(){
				this.current.ModelId = this.isEdit ? this.ModelId : 0;			// 产品型号id
				this.current.CategoryId = this.isEdit ? this.CategoryId : 0;	// 产品分类id
			},
			// ================================================ 监听事件 ==========================================
			/** 隐藏弹出框
			 * @param {Object} e
			 */
			hideModal: function(e) {
				// 隐藏hide
				this.ModalName = '';
			},
			/** 修改名称
			 * @param {Object} e
			 */
			changName: function(e) {
				this.current.Name = e.detail.value;
			},
			/** 修改产品分类
			 * @param {Object} item 当前的数值
			 */
			changeCategoryId: function(item) {
				this.current.CategoryId = item.value;
			},
			/** 修改排序
			 * @param {Number} value 值
			 */
			changeSort: function(value) {
				this.current.Sort = value;
			},
			/** 添加/编辑 产品型号
			 * @param {Object} form
			 */
			save: function(form) {
				this.$refs[form]
					.submit()
					.then(res => {
						// 表单验证成功后执行方法
						if(_self.isEdit){
							_self.modelUpdate_request();			// 修改产品型号
						}else{
							_self.modelAdd_request();				// 添加产品型号
						}
					})
					.catch(errors => {
						console.log("验证不通过提示=>",errors);
					});
			},
			// ================================================ 请求方法 ==========================================
			/**
			 * 获取产品分类请求
			 */
			categories_request: function() {
				setTimeout(function(){
					_self.category_list = changeobj.change(categories.list, "categoryId", "name", "children");
					var temp = {
						"value": 0,
						"text": "无",
					};
					_self.category_list.unshift(temp);
				} , 3000);
			},
			/** 获取当前产品型号
			 * @param {int} ModelId	产品型号id
			 */
			model_request : function(ModelId){
				console.log("获取产品型号接口未完成=>API");
				uni.showLoading();
				setTimeout( function(){
					uni.hideLoading();
					_self.current.Name = 'HP1108';
					_self.current.CategoryId = 3;
				} , 3000);
			},
			/**
			 * 添加产品型号请求
			 */
			modelAdd_request: function() {
				uni.showLoading();
				setTimeout(function(){
					uni.hideLoading();
					uni.showToast({
						title:'假装添加产品型号成功！'
					});
					_self.hideModal();
				},3000);
			},
			/**
			 * 编辑产品型号请求
			 */
			modelUpdate_request: function() {
				uni.showLoading();
				setTimeout(function(){
					uni.hideLoading();
					uni.showToast({
						title:'假装编辑产品型号成功！'
					});
					_self.hideModal();
				},3000);
			}
		}
	}
</script>

